@font-face {
    font-family: 'Nippo';
    src: url(../../../assets/fonts/nippo-font/Fonts/WEB/fonts/Nippo-Extralight.ttf);
}

@font-face {
    font-family: 'NippoBold';
    src: url(../../../assets/fonts/nippo-font/Fonts/WEB/fonts/Nippo-Bold.ttf);
}

.Profile {
    width: 771.77px;
    background: rgba(179, 175, 175, 0.12);
    border-radius: 6.45525px;
    margin-top: 25px;
}

.profile_container {

    margin: auto;
    text-align: center;
}

.profile-box img {
    margin-top: 31px;
    width: 327.43px;
}

.profile-box h3 {
    font-family: 'NippoBold';
    margin-top: 42px;
    font-size: 49.6337px;
    line-height: 46px;
    color: #FFFFFF;
    font-weight: bold;
    text-shadow: -1px -2px 1px #EA3498, 2px 2px 1px rgb(25, 225, 242);
    letter-spacing: 0.277671px;
}

.box2 {
    margin-top: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box2 img {
    max-width: 30px;
    margin: 0 8px 0 0;
    opacity: 0.4;
}

.box2 p {
    font-family: 'NippoBold';
    font-size: 30.5438px;
    line-height: 41px;
    color: #FFFFFF;
    opacity: 0.4;
}

.box3 {
    position: relative;
    align-items: center;
    justify-content: center;
    display: flex;
    margin-top: 54px;
}

.box3::after {
    content: url(../../../assets/img/arrow-right.png);
    position: absolute;
    top: 43px;
    right: 64px;


}

.box3::before {
    content: url(../../../assets/img/arrow-left.png);
    position: absolute;
    top: 43px;
    left: 64px;
}

.one {
    background: rgba(210, 69, 158, .2);
    padding: 34px 30px;
    border-radius: 50%;
}

.one img {
    max-width: 45px;
}

.two {
    background: rgba(210, 69, 158, .2);
    padding: 36px 34px;
    border-radius: 50%;
}

.two img {
    max-width: 45px;

}

.hr2 {
    height: 164px;
    border: 1px solid #FFFFFF;
    opacity: 0.1;
    margin: 0 97px 0 95px;
}

.box3-block-item p {
    font-family: 'Nippo';
    font-size: 30px;
    margin-top: 22px;
    line-height: 46px;
    color: #FFFFFF;
}

.box4 {
    position: relative;
    background-image: url(../../../assets//img/circle.png);
    background-size: 100% 100%;
    width: 100%;
    height: 600px;
    background-position: center;
    margin-top: 0;
    padding: 0;
}

.box4 img {
    margin: 0 6px 0 0;
}

.box5 {
    position: absolute;
bottom: 180px;
    left: 160px;
    margin-bottom: -50px;
}

.button {
    background: linear-gradient(101.44deg, #19E1F2 14.32%, #6CE7DD 98.22%);
    border-radius: 4.75904px;
    display: flex;
    align-items: center;
    padding: 15px 57px 16px 77px;
}

@media screen and (max-width:3000px) {
    .box5 {
        left: 210px;
    }

}

@media screen and (max-width:2750px) {
    .box5 {
        left: 190px;
    }

}

@media screen and (max-width:2560px) {
    .profile-box img {
        max-width: 222px;
        margin-top: 51px;
    }

    .profile-box h3 {
        margin-top: 55px;
        font-size: 33px;
        line-height: 31px;
    }

    .box2 {
        margin-top: 13px;
    }

    .box2 p {
        font-size: 20px;
        line-height: 28px;
    }

    .box2 img {
        max-width: 20px;

    }

    .box3 {
        margin-top: 31px;
    }

    .box3::after {
        top: 44px;
        right: 45px;

    }

    .box3::before {
        top: 44px;
        left: 45px;
    }

    .one {
        padding: 22px 20px;
    }

    .one img {
        max-width: 30px;
    }

    .two {
        padding: 24px;
    }

    .two img {
        max-width: 25px;
    }

    .hr2 {
        height: 111px;
        margin: 30px 64px;
    }

    .box3-block-item p {
        font-size: 20px;
        margin-top: 15px;
        line-height: 31px;
    }

    .box4 {

        height: 600px;
        margin-top: -24px;
    }

    .box5 {
        left: 100px;
    }

    .button {
        padding: 20px 79px 22px 104px;
    }
}


