        margin-top: 11px;
    }

    .box2 p {
        font-size: 15px;
        line-height: 21px;
    }

    .box2 img {
        max-width: 15px;

    }

    .box3 {
        margin-top: 26px;
    }

    .box3::after {
        top: 43px;
        right: 34px;

    }

    .box3::before {
        top: 43px;
        left: 34px;
    }

    .one {
        padding: 17px 15px;
    }

    .one img {
        max-width: 22px;
    }

    .two {
        padding: 17px;
    }

    .two img {
        max-width: 19px;
    }

    .hr2 {
        height: 81px;
        margin: 26px 47px;
    }

    .box3-block-item p {
        font-size: 20px;
        margin-top: 15px;
        line-height: 31px;
    }

    .box4 {
        width: 100%;
        height: 600px;
        margin-top: -24px;
    }

    .box5 {
        left: 70px;
    }

    .button {
        padding: 15px 57px 16px 77px;
    }
}

@media screen and (max-width:1500px) {
    .box5 {
        left: 50px;
    }
}

@media screen and (max-width:1440px) {
    .profile-box img {
        max-width: 138px;
        margin-top: 31px;
    }

    .profile-box h3 {
        margin-top: 34px;
        font-size: 21px;
        line-height: 19px;
    }

    .box2 {
        margin-top: 8px;
    }

    .box2 p {

        font-size: 15px;
        line-height: 21px;
    }

    .box2 img {
        max-width: 15px;

    }

    .box3 {
        margin-top: 29px;
    }

    .box3::after {
        top: 35px;
        right: 28px;

    }

    .box3::before {
        top: 35px;
        left: 28px;
    }

    .one {
        padding: 14px;
    }

    .one img {
        max-width: 19px;
    }

    .two {
        padding: 14px;
    }

    .two img {
        max-width: 16px;
    }

    .hr2 {
        height: 69px;
        margin: 28px 40px;
    }

    .box3-block-item p {
        font-size: 13px;
        margin-top: 9px;
        line-height: 19px;
    }

    .box4 {
        width: 100%;
        height: 600px;
        margin-top: -24px;
    }

    .box5 {
        left: 30px;
    }

    .button {
        padding: 13px 49px 14px 65px;
    }
}